/**
 * 
 * @param value 
 * @returns {boolean}
 */
export const isNumber = value => {
  return /(0|^[1-9]\d*$)/.test(value);
};

/**
 * ()
 * @param plateNum 
 * @returns {boolean}
 */
export const isPlateNum = plateNum => {
  return /^(([][A-Z](([0-9]{5}[DF])|([DF]([A-HJ-NP-Z0-9])[0-9]{4})))|([][A-Z][A-HJ-NP-Z0-9]{4}[A-HJ-NP-Z0-9]))$/.test(
    plateNum
  );
};

/**
 * ()
 * @param liter 
 * @returns {boolean}
 */
export const isLiter = liter => {
  return /[\u4e00-\u9fa5]/.test(liter);
};

/**
 * 
 * @return {boolean}
 */
export const isMobile = () => {
  return /Android|webOS|iPhone|iPod|BlackBerry|RIM|Windows Phone|Nexus|Nokia|iPad/i.test(
    navigator.userAgent
  );
};
